.teamContainer {
  background-color: #210A1E;
  margin: 4rem 0 4rem 0;
  width: 100%;
}

.teamHeader {
  padding-left: 10vw;
}

.blueLine {
  background: linear-gradient(180deg, #EFBF04 0%, rgba(44, 76, 157, 0) 92.74%);

  width: 80vw;
  height: 18vh;
  z-index: 5;
}


.HeaderInfo {
  color: #E5D536;
  font-weight: 700;
  font-size: 6rem;
  transform: translateY(-50%);
  z-index: 8;
}

/* new middle div */
.MiddleDiv {
  width: 90%;
  margin: auto;
  display: flex;
  flex-wrap: wrap; /* Allow items to wrap onto new lines */
  justify-content: center; /* Center items horizontally */
  align-items: center; /* Center items vertically */
  gap: 5%; /* Adjust the gap between items */
  margin-bottom: 0vh;
}

.MiddleDiv > .Card {
  width: 200px; /* Set a fixed width for the cards */
  margin: 1vh 10px; /* Space between cards */
}

@media (max-width: 720px) {
  .MiddleDiv {
    flex-direction: column; /* Stack items vertically on small screens */
  }

  .MiddleDiv > .Card {
    width: 80%; /* Adjust card width for smaller screens */
  }
}



@media (max-width: 720px) {
  .MiddleDiv {
    column-gap: 20px;
    row-gap: 7vh;
    grid-template-columns: repeat(3, 1fr);
  }
}